<script lang="ts">
  import { onMount } from "svelte";
  import { chatStore, clearMessages, resetFilters } from "./lib/stores/chat";
  import { userStore } from "./lib/stores/user";
  import ChatView from "./lib/components/ChatView.svelte";
  import { RefreshCw } from "lucide-svelte";
  import type { WidgetSettings } from "./lib/types";

  interface Props {
    settings: WidgetSettings;
  }

  const { settings } = $props();

  const defaultMessage =
    "Hi, I'm Fluent ChatBot assistant, ask me anything about Fluent products";

  onMount(() => {
    // Initialize user ID
    userStore.initializeUser();

    // Add initial bot message locally only if first_message exists
    if (settings.first_message) {
      chatStore.update((store) => ({
        ...store,
        messages: [
          {
            id: Date.now().toString(),
            chatId: "",
            role: "ai",
            message: settings.first_message,
            formattedMessage: settings.first_message,
            context: null,
            created_at: new Date().toISOString(),
          },
        ],
      }));
    }
  });

  const resetChat = () => {
    clearMessages();
    resetFilters();
    // Only add initial message if first_message exists
    if (settings.first_message) {
      chatStore.update((store) => ({
        ...store,
        messages: [
          {
            id: Date.now().toString(),
            chatId: "",
            role: "ai",
            message: settings.first_message,
            formattedMessage: settings.first_message,
            context: null,
            created_at: new Date().toISOString(),
          },
        ],
      }));
    }
  };
</script>

<main>
  <!-- Header -->
  <header class="main-header" style="background-color: {settings.color_hex};">
    <div class="title">{settings.bot_name}</div>
    <div class="button-group">
      <button class="icon-button" aria-label="Refresh chat" onclick={resetChat}>
        <RefreshCw class="icon" />
      </button>
    </div>
  </header>

  <div class="content">
    <ChatView {settings} />
  </div>
</main>

