<script lang="ts">
  import {
    MessageSquare,
    MessageSquareMore,
    Monitor,
    LifeBuoy,
    HelpCircle,
    Menu,
    Info,
    FileText,
    X,
  } from "lucide-svelte";

  const {
    isOpen,
    onToggle,
    buttonIcon,
    showButtonText,
    buttonText,
    colorHex,
    lighterColorHex,
  } = $props();

  const iconMap = {
    MessageSquare: MessageSquare,
    MessageSquareMore: MessageSquareMore,
    Monitor: Monitor,
    LifeBuoy: LifeBuoy,
    HelpCircle: HelpCircle,
    Menu: Menu,
    Info: Info,
    FileText: FileText,
  };

  const ButtonIcon =
    iconMap[buttonIcon as keyof typeof iconMap] || MessageSquare;
</script>

<button
  id="chat-button"
  class:active={isOpen}
  class:with-text={showButtonText}
  onclick={onToggle}
  aria-label={isOpen ? "Close chat" : "Open chat"}
  style="background-color: {colorHex}; --hover-color: {lighterColorHex}"
>
  {#if showButtonText}
    <span class="button-text">{buttonText}</span>
  {/if}
  <div class="chat-icon">
    {#if ButtonIcon}
      <ButtonIcon size={24} strokeWidth={2} />
    {/if}
  </div>
  <div class="close-icon">
    <X size={24} strokeWidth={2} />
  </div>
</button>

