<script lang="ts">
  import { chatStore, sendMessage } from "../stores/chat";
  import Message from "./Message.svelte";
  import { onMount } from "svelte";

  const { settings } = $props();
  let messageInput = $state("");
  let chatContainer: HTMLDivElement;
  let textarea: HTMLTextAreaElement;

  let messages = $derived($chatStore.messages);
  let isLoading = $derived($chatStore.isLoading);

  const scrollToBottom = () => {
    if (chatContainer) {
      chatContainer.scrollTop = chatContainer.scrollHeight;
    }
  };

  // Scroll to bottom when messages change
  $effect(() => {
    if (messages) {
      setTimeout(scrollToBottom, 100); // Small delay to ensure content is rendered
    }
  });

  // Scroll to bottom on initial load
  onMount(() => {
    scrollToBottom();
  });

  // Scroll to bottom after each update
  $effect(() => {
    scrollToBottom();
  });

  const adjustTextareaHeight = () => {
    if (textarea) {
      textarea.style.height = "auto";
      const maxHeight = 120; // 5 lines * 24px (line-height)
      const newHeight = Math.min(textarea.scrollHeight, maxHeight);
      textarea.style.height = `${newHeight}px`;
    }
  };

  $effect(() => {
    adjustTextareaHeight();
  });

  const handleSubmit = async () => {
    if (!messageInput.trim()) return;

    const content = messageInput;
    messageInput = "";
    adjustTextareaHeight();

    await sendMessage(content, settings);
  };
</script>

<div class="chat-container">
  <!-- Chat Container -->
  <div bind:this={chatContainer} class="messages-container">
    {#each messages as message (message.id)}
      {#if message.id === "streaming"}
        <div class="message-container bot-message">
          <div class="message-content bot-content">
            {#if settings.bot_icon}
              <div
                class="avatar"
                style="background-color: {settings.color_hex}"
              >
                <span>B</span>
              </div>
            {/if}
            <div class="bubble-container">
              <div class="message-bubble bot-bubble">
                {#if !message.message}
                  <div class="thinking">
                    <span>Thinking</span>
                    <div class="dots-container">
                      <span class="dot"></span>
                      <span class="dot"></span>
                      <span class="dot"></span>
                    </div>
                  </div>
                {:else}
                  <div class="prose typing-animation">
                    {@html message.message}
                  </div>
                {/if}
              </div>
            </div>
          </div>
        </div>
      {:else}
        <Message
          content={message.message}
          type={message.role === "human" ? "user" : "bot"}
          timestamp={new Date(message.created_at)}
          context={message.context || null}
          botIcon={settings.bot_icon}
          showSources={settings.show_sources}
          colorHex={settings.color_hex}
          lighterColorHex={settings.lighter_color_hex}
        />
      {/if}
    {/each}
  </div>

  <!-- Input Area -->
  <form
    onsubmit={(e) => {
      e.preventDefault();
      handleSubmit();
    }}
    class="input-container"
  >
    <textarea
      bind:this={textarea}
      bind:value={messageInput}
      placeholder="Type your message..."
      class="message-input"
      rows="1"
      oninput={adjustTextareaHeight}
      onkeydown={(e) => {
        if (e.key === "Enter") {
          if (!e.shiftKey) {
            e.preventDefault();
            handleSubmit();
          }
        }
      }}
      disabled={isLoading}
    ></textarea>
  </form>
</div>

