    box-sizing: border-box;
    min-height: 0; /* Important for flex container */
  }

  .message-container {
    display: flex;
    margin-bottom: 0.25rem;
    padding: 0 0.5rem;
  }

  .bot-message {
    justify-content: flex-start;
  }

