import { writable, derived, get } from 'svelte/store';
import type { Message } from '../types';
import api from '../utils/api';
import { formatMarkdown } from '../utils/markdown';

export interface ChatStore {
    messages: Message[];
    isLoading: boolean;
    currentChatId: string | null;
    error: string | null;
}

const initialState: ChatStore = {
    messages: [],
    isLoading: false,
    currentChatId: null,
    error: null
};

export const chatStore = writable<ChatStore>(initialState);

// Derived stores for computed values
export const formattedMessages = derived(chatStore, ($store) =>
    $store.messages.map(msg => ({
        ...msg,
        message: msg.role === 'ai' ? formatMarkdown(msg.message) : msg.message
    }))
);


export const sendMessage = async (content: string, settings: { team_id: string; bot_id: string }) => {
    const userId = localStorage.getItem('userId');
    const teamId = settings.team_id;
    const botId = settings.bot_id;

    if (!userId || !teamId || !botId) {
        throw new Error('User ID, Fluent Bot User ID, and Bot ID are required');
    }

    let previousText = '';
    let accumulatedText = '';

    // Add user message
    chatStore.update(state => ({
        ...state,
        messages: [
            ...state.messages,
            {
                id: Date.now().toString(),
                chatId: state.currentChatId || '',
                role: 'human',
                message: content,
                context: null,
                created_at: new Date().toISOString(),
            },
            {
                id: 'streaming',
                chatId: state.currentChatId || '',
                role: 'ai',
                message: '',
                context: null,
                created_at: new Date().toISOString(),
            },
        ],
        isLoading: true,
    }));

    try {
        const body = {
            message: content,
            userId: userId,
            teamId: teamId,
            botId: botId,
            chatId: get(chatStore).currentChatId || undefined
        };

        await api.post('/chat', body, {
            responseType: 'text',
            headers: {
                Accept: 'text/event-stream',
            },
            onDownloadProgress: (progressEvent) => {
                const text = progressEvent.event?.target?.responseText || '';

                if (!text || text === previousText) return;

                // Get only the new content
                const newContent = text.slice(previousText.length);
                previousText = text;

                // Split and process only the new content
                const lines = newContent.split('\n');
                let currentEvent = '';
                let currentData = '';

                for (const line of lines) {
                    // Check for event type
                    if (line.startsWith('event:')) {
                        currentEvent = line.replace('event:', '').trim();
                        continue;
                    }

                    // Check for data
                    if (line.startsWith('data:')) {
                        currentData = line.replace('data:', '').trim();

                        if (!currentData) continue;

                        try {
                            const parsedData = JSON.parse(currentData);
                            chatStore.update(s => {
                                const streamingMsg = s.messages.find(m => m.id === 'streaming');
                                if (!streamingMsg) return s;

                                // Handle AI response event
                                if (currentEvent === 'ai-response') {
                                    accumulatedText += parsedData;
                                    return {
                                        ...s,
                                        messages: s.messages.map(m =>
                                            m.id === 'streaming'
                                                ? {
                                                    ...m,
                                                    message: formatMarkdown(accumulatedText)
                                                }
                                                : m
                                        )
                                    };
                                }

                                // Handle chat ID event
                                if (currentEvent === 'chat-id' && 'chatId' in parsedData) {
                                    return {
                                        ...s,
                                        currentChatId: parsedData.chatId
                                    };
                                }

                                // Handle done event
                                if (currentEvent === 'done' && 'done' in parsedData && parsedData.done) {
                                    const finalMessage: Message = {
                                        id: Date.now().toString(),
                                        chatId: s.currentChatId || '',
                                        role: 'ai',
                                        message: formatMarkdown(accumulatedText),
                                        context: streamingMsg.context,
                                        created_at: new Date().toISOString()
                                    };
                                    return {
                                        ...s,
                                        messages: s.messages.map(m =>
                                            m.id === 'streaming'
                                                ? finalMessage
                                                : m
                                        ),
                                        isLoading: false
                                    };
                                }

                                // Handle error event
                                if ('error' in parsedData) {
                                    throw new Error(parsedData.error);
                                }

                                return s;
                            });
                        } catch (e) {
                            console.error('Error parsing SSE data:', e);
                            if (e instanceof Error && e.message !== 'Error parsing SSE data:') {
                                throw e;
                            }
                        }
                    }
                }
            },
        });
    } catch (error) {
        console.error('Error sending message:', error);
        chatStore.update(s => ({
            ...s,
            isLoading: false
        }));
        throw error;
    }
};

export const clearMessages = () => {
    chatStore.update(state => ({
        ...state,
        messages: [],
        currentChatId: null,
    }));
};

export const resetFilters = () => {
    chatStore.update(state => ({
        ...state,
        messages: [],
        currentChatId: null,
    }));
}; 