<script lang="ts">
  import {
    MessageSquare,
    MessageSquareMore,
    Monitor,
    LifeBuoy,
    HelpCircle,
    Menu,
    Info,
    FileText,
  } from "lucide-svelte";

  const {
    content,
    type,
    timestamp,
    context,
    botIcon,
    showSources,
    colorHex,
    lighterColorHex,
  } = $props();

  const iconMap = {
    MessageSquare: MessageSquare,
    MessageSquareMore: MessageSquareMore,
    Monitor: Monitor,
    LifeBuoy: LifeBuoy,
    HelpCircle: HelpCircle,
    Menu: Menu,
    Info: Info,
    FileText: FileText,
  };

  const BotIcon = $derived(
    botIcon && iconMap[botIcon as keyof typeof iconMap]
      ? iconMap[botIcon as keyof typeof iconMap]
      : MessageSquare
  );

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" });
  };

  const handleLinkClick = (e: MouseEvent, url: string) => {
    e.preventDefault();
    e.stopPropagation();
    window.open(url, "_blank", "noopener,noreferrer");
  };

  // Function to process markdown content and add click handlers to links
  const processMarkdownContent = (html: string) => {
    const parser = new DOMParser();
    const doc = parser.parseFromString(html, "text/html");
    const links = doc.getElementsByTagName("a");

    Array.from(links).forEach((link) => {
      const href = link.getAttribute("href");
      if (href) {
        link.setAttribute("target", "_blank");
        link.setAttribute("rel", "noopener noreferrer");
        link.addEventListener("click", (e) => handleLinkClick(e, href));
      }
    });

    return doc.body.innerHTML;
  };
</script>

<div
  class="message-container {type === 'user' ? 'user-message' : 'bot-message'}"
>
  <div
    class="message-content {type === 'user' ? 'user-content' : 'bot-content'}"
  >
    {#if botIcon && (type === "user" || type === "bot")}
      <div
        class="avatar"
        style="background-color: {type === 'user' ? lighterColorHex : colorHex}"
      >
        {#if type === "user"}
          <span>U</span>
        {:else if BotIcon}
          <BotIcon size={20} strokeWidth={2} color="white" />
        {/if}
      </div>
    {/if}
    <div class="bubble-container">
      <div
        class="message-bubble {type === 'user' ? 'user-bubble' : 'bot-bubble'}"
        style="--message-color: {type === 'user'
          ? lighterColorHex
          : 'white'}; --text-color: {type === 'user' ? 'white' : '#1f2937'}"
      >
        {#if type === "user"}
          <div class="message-text">{content}</div>
        {:else}
          <div class="markdown-content">
            {@html processMarkdownContent(content)}
          </div>
        {/if}
      </div>
      {#if context && context.length > 0 && showSources}
        <div class="context-links">
          <div class="context-label">Sources:</div>
          {#each context as source}
            <a
              href={source.url}
              target="_blank"
              rel="external"
              class="source-link"
              data-sveltekit-preload-data="tap"
            >
              <span class="link-text">{source.title}</span>
            </a>
          {/each}
        </div>
      {/if}
      <div
        class="timestamp {type === 'user' ? 'user-timestamp' : 'bot-timestamp'}"
      >
        {formatTime(timestamp)}
      </div>
    </div>
  </div>
</div>

